import { Component, OnInit } from '@angular/core';
import { ColumnService } from './service/column.service';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';

@Component({
  selector: 'app-column',
  templateUrl: './column.component.html',
  styleUrls: ['./column.component.css']
})
export class ColumnComponent implements OnInit {
  public column = 'name';
  public colObj = {column:""};
  constructor(public columnService:ColumnService,public toastr:ToastsManager) { }

  ngOnInit() {
    if(localStorage.getItem('column') && localStorage.getItem('column') != null){
      this.column = localStorage.getItem('column');
      this.colObj.column = this.column;
      this.columnService.setColumn(this.colObj)
                      .then(
                        (res) =>{
                           console.log(res);
                       },err =>{
                         console.log(err);
                       });
    }
  }
  submitColumn(){
    localStorage.setItem('column',this.column);
    this.colObj.column = this.column;
    this.columnService.setColumn(this.colObj)
                    .then(
                      (res) =>{
                         console.log(res);
                           this.toastr.success('Export column has been set successfully!', 'Success!');
                     },err =>{
                       console.log(err);
                         this.toastr.error('Export column not set!', 'Oops!');
                     });
    console.log(this.column);
  }

}



