import { Component, OnInit } from '@angular/core';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';
import {TableService} from './service/table.service';

@Component({
  selector: 'app-tables',
  templateUrl: './tables.component.html',
  styleUrls: ['./tables.component.css']
})
export class TablesComponent implements OnInit {
  public tableArray = [{table:""}];
  public table;
  public toggle = false;
  public mainTables = [{table:""}];
  public obj = {tables:[]};
  public tableToggle = false;
  constructor(public toastr:ToastsManager,public tableService:TableService) { }

  ngOnInit() {
      if(localStorage.getItem('mainTables') != '' && localStorage.getItem('mainTables') != null){
        this.mainTables = JSON.parse(localStorage.getItem('mainTables'));
        this.toggle = true;
        this.obj = {tables:this.mainTables};
        this.tableService.setTables(this.obj)
                       .then(
                         (res) =>{
                            console.log(res);

                        },err =>{
                          console.log(err);
                        });
      }
      this.tableArray.forEach(tab=>{
        if(tab.table === ''){
        this.tableArray.splice(0,1);
        }
      })
  }

  submitTable(tableData){
    this.table = tableData.value;
    if(localStorage.getItem('mainTables') != '' && localStorage.getItem('mainTables') != null){
      this.mainTables.forEach(element=>{
        if(element.table === this.table.table){
          this.tableToggle = true;
        }else{
          this.tableToggle = false;
        }
      })
      if(!this.tableToggle){
        this.mainTables.push(this.table);
        // console.log(this.mainTables);
        localStorage.setItem('mainTables',JSON.stringify(this.mainTables));
        this.obj = {tables:this.mainTables};
        this.tableService.setTables(this.obj)
                       .then(
                         (res) =>{
                            console.log(res);
                            this.toastr.success('Table has been added successfully!', 'Success!');
                            this.toggle = true;

                        },err =>{
                          console.log(err);
                          this.toastr.error('Table not added!', 'Oops!');
                          this.toggle = true;
                        });

      }else{
        this.toastr.error('Table already added!', 'Oops!');
      }

    }else{
      this.tableArray.forEach(element=>{
        if(element.table === this.table.table){
          this.tableToggle = true;
        }else{
          this.tableToggle = false;
        }
      })
      if(!this.tableToggle){
        this.tableArray.push(this.table);
        // console.log("mainTables.......");
        // console.log(this.mainTables);
        this.mainTables = this.tableArray;
        localStorage.setItem('mainTables',JSON.stringify(this.mainTables));
        this.obj = {tables:this.mainTables};
        this.tableService.setTables(this.obj)
                       .then(
                         (res) =>{
                            console.log(res);
                            this.toastr.success('Table has been added successfully!', 'Success!');
                            this.toggle = true;

                        },err =>{
                          console.log(err);
                          this.toastr.error('Table not added!', 'Oops!');
                          this.toggle = true;

                        });
      }else{
        this.toastr.error('Table already added!', 'Oops!');
      }

    }


}

  remove(i){
    this.mainTables.splice(i,1);
    console.log(this.mainTables);
    localStorage.setItem('mainTables',JSON.stringify(this.mainTables));
    this.obj = {tables:this.mainTables};
    this.tableService.setTables(this.obj)
                   .then(
                     (res) =>{
                        console.log(res);
                        this.toastr.success('Table has been removed!', 'Success!');

                    },err =>{
                      console.log(err);
                      this.toastr.error('Table not removed!', 'Oops!');

                    });
  }

}



