import { Injectable } from '@angular/core';
import { Http,Response } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Headers, RequestOptions,ResponseContentType } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
declare var $ :any;
import { saveAs } from 'file-saver';

@Injectable()
export class ScheduleService {
   headers: Headers;
   options: RequestOptions;

  constructor(public http:Http) {
    console.log('data service connected');
    this.headers      = new Headers({ 'Content-Type': 'application/json'});
    this.options       = new RequestOptions({ headers: this.headers });
  }

  setScheduleTime(timeObj:Object):Promise<any>{
     var localApiUrl = 'http://localhost:3000';
     const url = localApiUrl+"/api/set-time";
     return this.http.post(url,timeObj,this.options).toPromise()
             .then(this.extractData)
                   .catch(this.handleErrorPromise);;
  }


   private extractData(res: Response) {
     console.log(res);
        let body = res.json();
        return body || {};
       }

  // // below function can be used when deal with Observable
    private handleErrorPromise (error: Response | any) {
      console.log(error || error);
	     //console.error(error.message || error);
	      return Promise.reject(error || error.error);
    }


}



