import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule} from '@angular/forms';
import { HttpModule} from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';
import {ToastModule, ToastOptions} from 'ng2-toastr/ng2-toastr';
import { CustomOption } from './custom-option';
import {SharedService} from './service/shared.service';
import { LoginService } from './components/login/service/login.service';
import { DashboardService } from './components/dashboard/service/dashboard.service';
import { ManageService } from './components/manage-export/service/manage.service';
import { TableService } from './components/tables/service/table.service';
import { ColumnService } from './components/column/service/column.service';
import { URLService } from './components/url/service/url.service';
import { DirectoryService } from './components/export-directory/service/directory.service';
import { ScheduleService } from './components/schedule-pdf-export/service/schedule.service';
import { AppComponent } from './app.component';
import { LoginComponent } from './components/login/login.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LayoutComponent } from './components/layout/layout.component';
import { SettingLayoutComponent } from './components/setting-layout/setting-layout.component';
import { UrlComponent } from './components/url/url.component';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import {BrowserAnimationsModule,NoopAnimationsModule} from '@angular/platform-browser/animations';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import {DataTableModule} from "angular2-datatable";
import { DataFilterPipe }   from './service/data-filter.pipe';
import {AuthGuard} from './service/authguard.service';
import { FilterPipe} from './filter.pipe';
import {NgxPaginationModule} from 'ngx-pagination';
import { TablesComponent } from './components/tables/tables.component';
import { SchedulePdfExportComponent } from './components/schedule-pdf-export/schedule-pdf-export.component';
import { ExportDirectoryComponent } from './components/export-directory/export-directory.component';
import { ManageExportComponent } from './components/manage-export/manage-export.component';
import { ColumnComponent } from './components/column/column.component';
import { ThemeComponent } from './components/theme/theme.component';
import { LogoComponent } from './components/logo/logo.component';
import { FileUploadModule } from 'ng2-file-upload';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashboardComponent,
    LayoutComponent,
    HeaderComponent,
    FooterComponent,
    DataFilterPipe,
    FilterPipe,
    SettingLayoutComponent,
    UrlComponent,
    TablesComponent,
    SchedulePdfExportComponent,
    ExportDirectoryComponent,
    ManageExportComponent,
    ColumnComponent,
    ThemeComponent,
    LogoComponent

  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    Ng4LoadingSpinnerModule,
    BrowserAnimationsModule,
    NoopAnimationsModule,
    DataTableModule,
    NgxPaginationModule,
    FileUploadModule,
    ToastModule.forRoot(),
    RouterModule.forRoot([
      {
        path: '',
        component:LoginComponent
      },
    {
      path: 'admin',
      component:LayoutComponent,
      children:[
      {
        path: 'dashboard',
        component:DashboardComponent
      }
      ]
    },
    {
      path: 'settings',
      component:SettingLayoutComponent,
      children:[
      {
        path: 'url',
        component:UrlComponent
      },
      {
        path: 'tables',
        component:TablesComponent
      },
      {
        path: 'export-directory',
        component:ExportDirectoryComponent
      },
      {
        path: 'schedule',
        component:SchedulePdfExportComponent
      },
      {
        path: 'start-stop-export',
        component:ManageExportComponent
      },
      {
        path: 'column',
        component:ColumnComponent
      },
      {
        path: 'theme',
        component:ThemeComponent
      },
      {
        path: 'logo',
        component:LogoComponent
      }
      ]
    }
  ]),
  ],
  providers: [AuthGuard,SharedService,LoginService,DashboardService,ManageService,URLService,DirectoryService,TableService,ScheduleService,ColumnService,{provide: ToastOptions, useClass: CustomOption}],
  bootstrap: [AppComponent]
})
export class AppModule { }



