import { Component, OnInit } from '@angular/core';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';
import{ ScheduleService } from './service/schedule.service';

@Component({
  selector: 'app-schedule-pdf-export',
  templateUrl: './schedule-pdf-export.component.html',
  styleUrls: ['./schedule-pdf-export.component.css']
})
export class SchedulePdfExportComponent implements OnInit {
  public everyhour = 2;
  public hourObj = {time:""};
  constructor(public toastr:ToastsManager,public scheduleService:ScheduleService) { }

  ngOnInit() {
    if(localStorage.getItem('everyhour') != '' && localStorage.getItem('everyhour') != null){
      this.everyhour = JSON.parse(localStorage.getItem('everyhour'));
      this.hourObj.time = "every "+this.everyhour+" hour";
      this.scheduleService.setScheduleTime(this.hourObj)
                     .then(
                       (res) =>{
                          console.log(res);
                      },err =>{
                        console.log(err);
                      });
    }else{
      this.hourObj.time = "every 1 hour";
      this.scheduleService.setScheduleTime(this.hourObj)
                     .then(
                       (res) =>{
                          console.log(res);
                      },err =>{
                        console.log(err);
                      });
    }
  }

  submitTime(){
    console.log(this.everyhour);
    if(this.everyhour < 1){
      this.toastr.error('Minimum time is 1 hour!', 'Oops!');
      return;
    }
    localStorage.setItem('everyhour',JSON.stringify(this.everyhour));
    this.hourObj.time = "every "+this.everyhour+" hour";
    this.scheduleService.setScheduleTime(this.hourObj)
                   .then(
                     (res) =>{
                        console.log(res);
                        this.toastr.success('Scheduled for every '+this.everyhour +' hour!', 'Success!');
                    },err =>{
                      console.log(err);
                      this.toastr.error('Time not schedule!', 'Oops!');
                    });
  }

}



