import { Component, OnInit } from '@angular/core';
import {ManageService} from './service/manage.service';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';

@Component({
  selector: 'app-manage-export',
  templateUrl: './manage-export.component.html',
  styleUrls: ['./manage-export.component.css']
})
export class ManageExportComponent implements OnInit {
  public startToggle:boolean = false;
  public stopToggle:boolean = false;
  public toggle:boolean = false;
  public startDisabledToggle = false;
  public stopDisabledToggle = false;
  constructor(public manageService:ManageService,public toastr:ToastsManager) { }

  ngOnInit() {
    if(localStorage.getItem('startDisabledToggle') && localStorage.getItem('startDisabledToggle') != null){
      this.startDisabledToggle = JSON.parse(localStorage.getItem('startDisabledToggle'));
    }
    if(localStorage.getItem('stopDisabledToggle') && localStorage.getItem('stopDisabledToggle') != null){
      this.stopDisabledToggle = JSON.parse(localStorage.getItem('stopDisabledToggle'));
    }
  }

  startService(){
   //console.log("clicked");
   this.manageService.startService()
                     .subscribe(
                       (res) =>{
                         console.log(res.json());
                         this.stopToggle = false;
                         this.startToggle = true;
                         this.toggle = true;
                         this.startDisabledToggle = true;
                         this.stopDisabledToggle = false;
                         localStorage.setItem('startDisabledToggle',JSON.stringify(this.startDisabledToggle));
                         localStorage.setItem('stopDisabledToggle',JSON.stringify(this.stopDisabledToggle));
                       },err =>{
                         console.log(err.json());
                         if(err.json().statusCode === 402){
                           this.toastr.error(err.json().data+'!', 'Oops!');
                         }else{
                          this.toastr.error('Something went wrong, please try again later!', 'Oops!');
                         }
                       });
  }

  stopService(){
    this.manageService.stopService()
                      .subscribe(
                        (res) =>{
                          console.log(res.json());
                          this.stopToggle = true;
                          this.startToggle = false;
                          this.toggle = true;
                          this.startDisabledToggle = false;
                          this.stopDisabledToggle = true;
                          localStorage.setItem('startDisabledToggle',JSON.stringify(this.startDisabledToggle));
                          localStorage.setItem('stopDisabledToggle',JSON.stringify(this.stopDisabledToggle));
                        },err =>{
                          console.log(err);
                          this.stopToggle = true;
                          this.startToggle = false;
                          this.toggle = true;
                          this.startDisabledToggle = false;
                          this.stopDisabledToggle = true;
                          localStorage.setItem('startDisabledToggle',JSON.stringify(this.startDisabledToggle));
                          localStorage.setItem('stopDisabledToggle',JSON.stringify(this.stopDisabledToggle));
                        });
  }

}



