import { Component, OnInit,ViewChild } from '@angular/core';
import { FileUploader,FileLikeObject } from 'ng2-file-upload';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';
import { Router } from '@angular/router';
const URL = 'http://localhost:3000/api/upload/logo';

@Component({
  selector: 'app-logo',
  templateUrl: './logo.component.html',
  styleUrls: ['./logo.component.css']
})

export class LogoComponent implements OnInit {
  @ViewChild('fileInput') fileInput: any;
  public logo = '';
  public inputLogo = '';
  public errorMessage:string;
  // image variables
public allowedImageType = ['image/png', 'image/gif', 'image/jpeg'];
public maxImageSize = 8*1024*1024; // 8 MB
public imageUploader:FileUploader = new FileUploader({
  url: URL,
  itemAlias: 'photo',
  allowedMimeType:this.allowedImageType,
  maxFileSize:this.maxImageSize,
  autoUpload: false,
  removeAfterUpload:true
});
  constructor(public toastr:ToastsManager,public router:Router) {
    // image uploader functions
    this.imageUploader.onWhenAddingFileFailed = (item, filter, options) => this.onUploadImageFailed(item, filter, options);
    this.imageUploader.onAfterAddingFile = (fileItem)=> {
      fileItem.withCredentials = false;
      console.log(fileItem.file.type);
    };
    this.imageUploader.onCompleteItem = (item:any, response:any, status:any, headers:any) => {
         console.log("ImageUpload:uploaded status:", item, status, response);
          this.fileInput.nativeElement.value = '';
          this.inputLogo = '';
          //console.log(item);
         // console.log(status);
         // console.log((response));
         if(response){
          localStorage.setItem('imagePath',JSON.parse(response).data);
           this.toastr.success('Logo has been changed successfully!', 'Success!');
       }else{
         this.toastr.error('Logo not updated!', 'Oops!');
       }
     };
   }

  ngOnInit() {
  }


  onUploadImageFailed(item: FileLikeObject, filter: any, options: any) {
    //console.log(item.type);
       switch (filter.name) {
           case 'fileSize':
               this.errorMessage = 'Maximum upload size 8 MB allowed';
                 console.log(this.errorMessage);
               break;
           case 'mimeType':
               const allowedTypes = this.allowedImageType.join();
               this.errorMessage = 'Type ' +item.type+ ' is not allowed. Allowed types:' +allowedTypes;
                  console.log(this.errorMessage);
               break;
           default:
               this.errorMessage = 'Unknown error filter is ' +filter.name;
                  console.log(this.errorMessage);
       }
   }

   uploadImage(){
     this.imageUploader.uploadAll();
   }

  handleInputChange (event) {
      var image = event.target.files[0];
      console.log(image);
      this.inputLogo = image.name;
      var pattern = /image-*/;
      //var reader = new FileReader();
      if (!image.type.match(pattern)) {
          console.error('File is not an image');
          //of course you can show an alert message here
    }
  }

submitLogo(){
  console.log("-----");
  if(this.inputLogo == ''){
    this.toastr.error('Please select the logo!', 'Oops!');
    return;
  }
  this.uploadImage();
  //this.convertImageToBase64(this.logo);
 }


}



