import { Component, OnInit } from '@angular/core';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';
import {DirectoryService} from './service/directory.service';

@Component({
  selector: 'app-export-directory',
  templateUrl: './export-directory.component.html',
  styleUrls: ['./export-directory.component.css']
})
export class ExportDirectoryComponent implements OnInit {
  public directory = 'export';
  public dirObj = {directory:""};
  constructor(public toastr:ToastsManager,public directoryService:DirectoryService) { }

  ngOnInit() {
    if(localStorage.getItem('directory') && localStorage.getItem('directory') != null){
      this.directory = localStorage.getItem('directory');
      this.dirObj.directory = this.directory;
      this.directoryService.setDirectory(this.dirObj)
                      .then(
                        (res) =>{
                           console.log(res);
                       },err =>{
                         console.log(err);
                       });
    }
  }

  submitDirectory(){
    localStorage.setItem('directory',this.directory);
    this.dirObj.directory = this.directory;
    this.directoryService.setDirectory(this.dirObj)
                    .then(
                      (res) =>{
                         console.log(res);
                           this.toastr.success('Export directory has been set successfully!', 'Success!');
                     },err =>{
                       console.log(err);
                         this.toastr.error('Export directory not set!', 'Oops!');
                     });
    console.log(this.directory);
  }

}



