import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';
import {URLService} from './service/url.service';

@Component({
  selector: 'app-url',
  templateUrl: './url.component.html',
  styleUrls: ['./url.component.css']
})
export class UrlComponent implements OnInit {
  public url = 'https://www.cloudapps.services';
  public msg = {status:"",message:"",color:""};
  public clsToggle:boolean = false;
  public obj = {url:""};
  constructor(public router:Router,public toastr:ToastsManager,public urlService:URLService) {}

  ngOnInit() {
    if(localStorage.getItem('url') && localStorage.getItem('url') != null){
      this.url = localStorage.getItem('url');
      this.obj.url = this.url;
      this.urlService.setURL(this.obj)
                     .then(
                       (res) =>{
                          console.log(res);                        
                      },err =>{
                        console.log(err);
                      });
    }
  }

  submitUrl(){
    //console.log(this.url);
    var n = this.url.endsWith("/");
    if(n){
      this.msg.status = 'Failed!';
      this.msg.message = "Please enter the url without containing '/' at the end of url";
      this.msg.color = '#a94442';
      //this.toastr.error('This is not good!', 'Oops!');
    }else{
      this.obj.url = this.url;
      this.urlService.setURL(this.obj)
                     .then(
                       (res) =>{
                          console.log(res);
                          this.msg.status = 'Success!';
                          this.msg.message = 'URL has been set successfully, Please login to continue';
                          this.msg.color = '#3c763d';
                          localStorage.setItem('url',this.url);
                          this.toastr.success('URL has been set successfully, Please login to continue!', 'Success!');
                          this.router.navigate(['/']);
                      },err =>{
                        console.log(err);
                        this.msg.status = 'Failed!';
                        this.msg.message = "URL is not set properly, please try again later";
                        this.msg.color = '#a94442';
                      });

    }
  //  console.log(n);
  }

}



