import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastsManager, ToastOptions } from 'ng2-toastr/ng2-toastr';
import {LoginService} from './service/login.service';
import { SharedService } from './../../service/shared.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  public showAuthError:boolean = false;
  public errorMsg:string = '';
  public loading = false;
  public obj = {username:"",password:"",sessionId:""};
  public user = {username:"",password:""};
  public colorFlag = false;
  public imagePath = '';
  public logoName = 'Brixxs';
  public logoToggle = false;
  constructor(public loginService:LoginService,public router:Router,public toastr:ToastsManager,public sharedService:SharedService,private spinnerService: Ng4LoadingSpinnerService) {}

  ngOnInit() {
    if(localStorage.getItem('username') != null && localStorage.getItem('password') != null){
      this.user.username = localStorage.getItem('username');
      this.user.password = localStorage.getItem('password');
    }

    if(localStorage.getItem('imagePath') != '' && localStorage.getItem('imagePath') != null){
      this.imagePath = 'http://localhost:3000/api/fetch-logo?path='+localStorage.getItem('imagePath');
      this.logoToggle = true;
    }else{
      this.logoToggle = false;
    }

  }

  loginUser(formData){
    this.spinnerService.show();
       //console.log(formData.value);
      localStorage.setItem('username',formData.value.username);
      localStorage.setItem('password',formData.value.password);
      this.loginService.login(formData.value.username,formData.value.password).then(user1 =>{
        this.obj.username = formData.value.username;
        this.obj.password = formData.value.password;
        console.log(user1.sessionId);
        this.obj.sessionId = user1.sessionId;
        this.loginService.postLoginData(this.obj).then(res=>{
          console.log(res.json());
        }).catch(err=>{
          console.log(err);
        })
          formData.reset();
          this.spinnerService.hide();
          //console.log("toggle value : "+this.sharedService.toggleUser);
          this.router.navigate(['/admin/dashboard']);
      }).catch(error =>{
         console.log(error);
         this.spinnerService.hide();
         this.showAuthError = true;
         this.errorMsg = 'Incorrect username or password';
         //this.router.navigate(['/']);
      });
    }

    getImagePath(){
      if(localStorage.getItem('imagePath') != '' && localStorage.getItem('imagePath') != null){
        this.imagePath = 'http://localhost:3000/api/fetch-logo?path='+localStorage.getItem('imagePath');
        this.logoToggle = true;
        return this.imagePath;
      }else{
        this.logoToggle = false;
      }
    }

    getMyStyles() {
      if(localStorage.getItem('themecolor') != '' && localStorage.getItem('themecolor') != null){
        let myStyles = {
           'background-color': ''+this.colorFlag ? JSON.parse(localStorage.getItem('themecolor')).colorCode+'' : '#337ab7'
           // 'font-size.em': this.isSmall ? this.small/5 : this.big/5,
           // 'background-image': !this.isBackgroundRed ? 'url(\'images/red.gif\')' : 'url(\'images/green.gif\')'
        };
        return myStyles;
      }

  }

}



